@import "https://fonts.googleapis.com/css2?family=Inter&family=Roboto:wght@300;400;500;700&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --warningColor: #ff8d00;
  --whiteColor: #fff;
  --blackColor: #000;
  --mainColor: #22252e;
  --secondaryColor: #889099;
  --borderColor: #d7d8d9;
  --accentColor: #4cb649;
  --btnBgc: linear-gradient(0deg, #4cb649 0%, #5ddf5a 100%);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  color: var(--mainColor);
  letter-spacing: .02em;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  font-weight: 300;
  line-height: 1.42;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 450px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 738px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.btn {
  height: 40px;
  letter-spacing: .8px;
  text-transform: uppercase;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 30px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
}

.warning {
  background-color: var(--mainColor);
  height: 68px;
  padding: 14px 0 13px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .warning {
    height: 40px;
    align-items: center;
    padding: 0;
    display: flex;
  }
}

.warning__icon--alert {
  fill: var(--warningColor);
  margin-right: 4px;
}

.warning__icon--close {
  fill: var(--secondaryColor);
  position: absolute;
  top: 10px;
  right: 10px;
}

@media screen and (min-width: 768px) {
  .warning__icon--close {
    top: 8px;
    right: 16px;
  }
}

.warning__text {
  letter-spacing: .02px;
  color: var(--secondaryColor);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.33;
}

.warning__text--orange {
  color: var(--warningColor);
  align-items: center;
  margin-bottom: 9px;
  font-weight: 700;
  display: flex;
}

@media screen and (min-width: 768px) {
  .warning__text--orange {
    margin-bottom: 0;
    margin-right: 32px;
  }
}

@media screen and (min-width: 768px) {
  .warning__text--isp {
    margin-right: 32px;
  }
}

.warning__link {
  color: var(--whiteColor);
  margin-bottom: 13px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.33;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.warning__link:hover, .warning__link:focus {
  color: var(--accentColor);
}

@media screen and (min-width: 768px) {
  .warning__link {
    margin-bottom: 0;
  }
}

@media screen and (min-width: 768px) {
  .warning__wrapper {
    align-items: center;
    display: flex;
  }
}

.container__warning {
  justify-content: space-between;
  padding-right: 38px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .container__warning {
    justify-content: center;
    padding-right: 0;
  }
}

@media screen and (min-width: 768px) {
  .wrapper--first {
    margin-right: 32px;
  }
}

@media screen and (min-width: 768px) {
  .wrapper--second {
    flex-direction: row-reverse;
  }
}

.header {
  height: 70px;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header {
    height: 120px;
  }
}

.header__container {
  width: 100%;
  margin: 0 auto;
  padding: 0 12px 0 10px;
}

@media screen and (min-width: 480px) {
  .header__container {
