.nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav__menu {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
}

@media screen and (min-width: 768px) {
  .nav__menu {
    display: none;
  }
}

.nav__logo {
  color: var(--blackColor);
  font-family: Inter, sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.22;
}

.nav__btn {
  color: var(--whiteColor);
  background: var(--btnBgc);
  min-width: 126px;
  border: rgba(0, 0, 0, 0);
}

@media screen and (min-width: 768px) {
  .nav__btn {
    display: none;
  }
}

.nav__list {
  display: none;
}

@media screen and (min-width: 768px) {
  .nav__list {
    display: flex;
  }
}

.nav__item:not(:last-child) {
  margin-right: 30px;
}

.nav__link {
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #323642;
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  display: flex;
}

.nav__link--active {
  position: relative;
}

.nav__link--active:after {
  content: "";
  height: 3px;
  background-color: var(--accentColor);
  position: absolute;
  bottom: -6px;
  left: 0;
  right: 0;
}

.nav__icon {
  margin-left: 10px;
}

.nav__groupe {
  display: none;
}

@media screen and (min-width: 768px) {
  .nav__groupe {
    display: block;
  }
}

.nav__btn--secondary {
  color: var(--accentColor);
  width: 90px;
  border: 1px solid var(--accentColor);
  margin-right: 10px;
}

.nav__btn--primary {
  color: var(--whiteColor);
  border: 1px solid var(--accentColor);
  background-color: var(--accentColor);
  width: 160px;
}

.feature {
  padding: 31px 0 20px;
}

@media screen and (min-width: 768px) {
  .feature {
    padding-top: 0;
  }
}

.feature__img {
  width: 100%;
