  font-size: 12px;
  font-weight: 400;
  line-height: 1.33;
}

.feature__comments {
  color: var(--secondaryColor);
  align-items: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.57;
  display: flex;
}

.feature__icon {
  fill: var(--secondaryColor);
  margin-right: 9px;
}

.tags {
  padding-top: 30px;
  padding-bottom: 20px;
  display: none;
}

@media screen and (min-width: 768px) {
  .tags {
    display: block;
  }
}

.tags__wrapper {
  height: 60px;
  background: #f7f7f7;
  border-radius: 4px;
  align-items: center;
  padding: 0 33px 0 32px;
  display: flex;
}

.tags__title {
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #323642;
  margin-right: 43px;
  font-size: 14px;
