}

.tags__item:not(:last-child) {
  margin-right: 35px;
}

.tags__link {
  letter-spacing: .8px;
  text-transform: uppercase;
  color: var(--accentColor);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  display: block;
}

.tags__contacts {
  margin-left: auto;
}

.contacts {
  display: flex;
}

.contacts__link {
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #323642;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  display: block;
}

.contacts__link--first {
  margin-right: 35px;
}

@media screen and (min-width: 768px) {
  .blog {
    display: none;
  }
}

.blog__form {
  position: relative;
}

.blog__icon {
  position: absolute;
  top: 21px;
  left: 21px;
}

.blog__input {
  width: 100%;
  height: 59px;
  color: var(--mainColor);
  border: 1px solid var(--borderColor);
  background-color: #fff;
  border-radius: 4px;
  padding: 16px 15px 15px 58px;
  font-family: inherit;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.56;
  display: block;
}

.blog__input:hover ~ .blog__icon, .blog__input:focus ~ .blog__icon {
