}

.aside__title--wifi:before {
  content: "";
  height: 60px;
  width: 60px;
  background-image: url("wifi.ec22a1b5.png");
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
}

.aside__title--tag:not(:lang(ae)):not(:lang(ar)):not(:lang(arc)):not(:lang(bcc)):not(:lang(bqi)):not(:lang(ckb)):not(:lang(dv)):not(:lang(fa)):not(:lang(glk)):not(:lang(he)):not(:lang(ku)):not(:lang(mzn)):not(:lang(nqo)):not(:lang(pnb)):not(:lang(ps)):not(:lang(sd)):not(:lang(ug)):not(:lang(ur)):not(:lang(yi)) {
